@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300,400,500,700&display=swap');

html {
   body{
     padding: 0;
     margin: 0;
     font-family: "Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
   }
}

//skeleton

.hero{
  // visibility: hidden;
  // background-image: url(../src/founding-fathers.jpg);
  opacity: 100%;
  background-color: transparent;
  background-size: cover;
  height: 100vh;
  .container{
    width: 1440px;
    min-width: 1440px;
    margin: 0 auto;
    .hero-inner{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 24px;
      height: 100vh;
      .hero-content{
        width: 50%;
        .hero-content-inner{
          width: 400px;
          margin: 0 auto;
          color: black;
          z-index: -1;
          h1{
            font-weight: 500;
            font-size: 32px;
            margin-bottom: 30px;
            .hero-content-line{
                  margin: 0;
                  height: 44px;
                  overflow: hidden;
            }
          }
          p{
              font-size: 14px;
              line-height: 24px;
              font-weight: 500px;
              padding-right: 48px;
              margin-bottom: 20px;
          }
          .btn-row{
            position: relative;
            &:before {
              position: absolute;
              content: "";
              width: 56px;
              height: 1px;
              background: #dfdfdf;
              top: 50%;
              left: -68px;
            }
            button{
              background: none;
              border: none;
              font-size: 12px;
              padding: 0;
              cursor: pointer;
              text-transform: uppercase;
              font-weight: 700;
              letter-spacing: 0.7px;
              display: flex;
              align-items: center;
              position: relative;
              &:focus {
                outline: none;
              }
              .arrow-icon{
                
              }
            }
          }
        }

      }
      .hero-images{
        width: 50%;
        height: 110vh;
        left: 400px;
        overflow: hidden;
        position: fixed;
         .hero-images-inner{
          position: absolute;
           .hero-image-justice {
             margin-top: 90px;
              top: 0px;
              right: 0px;
           }
         }
      }
    }
  }
}

.Image {
  width: 2000px;
  height: 800px;
  animation: moveSpriteSheet 1s steps(4) infinite;
}
.Character {
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 600px;
  overflow: hidden;
}

@keyframes moveSpriteSheet {
   from {
      transform: translate3d(0px, 0, 0);
   }
   to{
    transform: translate3d(-100%, 0, 0);
   }
}

