@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300,400,500,700&display=swap);

  
  .bge {
    z-index: -1;
    position: fixed;
    background-size: cover;
    width: 100vw;
    height: 200vh;
    top: 0px;
    background-position: center;
    opacity: 70%;
    overflow: hidden;
  }


html body{padding:0;margin:0;font-family:"Poppins",-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}.hero{opacity:100%;background-color:transparent;background-size:cover;height:100vh}.hero .container{width:1440px;min-width:1440px;margin:0 auto}.hero .container .hero-inner{display:flex;align-items:center;justify-content:space-between;margin:0 24px;height:100vh}.hero .container .hero-inner .hero-content{width:50%}.hero .container .hero-inner .hero-content .hero-content-inner{width:400px;margin:0 auto;color:black;z-index:-1}.hero .container .hero-inner .hero-content .hero-content-inner h1{font-weight:500;font-size:32px;margin-bottom:30px}.hero .container .hero-inner .hero-content .hero-content-inner h1 .hero-content-line{margin:0;height:44px;overflow:hidden}.hero .container .hero-inner .hero-content .hero-content-inner p{font-size:14px;line-height:24px;font-weight:500px;padding-right:48px;margin-bottom:20px}.hero .container .hero-inner .hero-content .hero-content-inner .btn-row{position:relative}.hero .container .hero-inner .hero-content .hero-content-inner .btn-row:before{position:absolute;content:"";width:56px;height:1px;background:#dfdfdf;top:50%;left:-68px}.hero .container .hero-inner .hero-content .hero-content-inner .btn-row button{background:none;border:none;font-size:12px;padding:0;cursor:pointer;text-transform:uppercase;font-weight:700;letter-spacing:0.7px;display:flex;align-items:center;position:relative}.hero .container .hero-inner .hero-content .hero-content-inner .btn-row button:focus{outline:none}.hero .container .hero-inner .hero-images{width:50%;height:110vh;left:400px;overflow:hidden;position:fixed}.hero .container .hero-inner .hero-images .hero-images-inner{position:absolute}.hero .container .hero-inner .hero-images .hero-images-inner .hero-image-justice{margin-top:90px;top:0px;right:0px}.Image{width:2000px;height:800px;animation:moveSpriteSheet 1s steps(4) infinite}.Character{margin-left:auto;margin-right:auto;width:300px;height:600px;overflow:hidden}@keyframes moveSpriteSheet{from{transform:translate3d(0px, 0, 0)}to{transform:translate3d(-100%, 0, 0)}}

